
@import url('https://fonts.googleapis.com/css?family=Roboto:700');
html, body, #app {
  height: 100%;
  background: whitesmoke;
  margin: 0;
  padding: 0;
}
#app {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  font-family: 'Roboto', sans-serif;
  font-size: 32px;
}
* {
  -o-box-sizing: border-box;
  box-sizing: border-box;
}
.flex-container {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      background-color: white;
      -ms-flex-wrap: wrap;
          flex-wrap: wrap;
      width: 100%;
      max-width: 1000px;
      margin: 0 auto;
      border: solid 1px #eee;
      box-shadow: 0 0 5px #eee;
}
.box {
      background: #0060ff;
      color: white;
      height: 200px;
      margin: 10px;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
      -webkit-box-pack: center;
          -ms-flex-pack: center;
              justify-content: center;
}
.box p {
    text-align: center;
}
.box:hover {
    opacity: .9;
}
.box.small {
      width: calc(25% - 20px);
}
.box.wide {
      width: calc(50% - 20px);
}
.box.tall {
}
.box.map {
      height: 100%;
      width: 100%;
}
iframe {
      width: 100%;
      min-height: 400px;
}
a {
      display: block;
      height: 100%;
      width: 100%;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
      -webkit-box-pack: center;
          -ms-flex-pack: center;
              justify-content: center;
      text-decoration: none;
      color: white;
      cursor: pointer;
}
@media screen and (max-width: 1000px) {
.flex-container .box {
      width: calc(50% - 20px);
}
.flex-container .box.map {
      width: calc(100% - 20px);
}
#app {
      -webkit-box-align: baseline;
          -ms-flex-align: baseline;
              align-items: baseline;
}
#weather, #list {
      -webkit-animation: none!important;
              animation: none!important;
}
}
@media screen and (max-width: 500px) {
.flex-container .box {
      width: calc(100% - 20px);
}
}
::-webkit-scrollbar {
    display: none;
}



